  .frontend-area {
    min-height: 150px;
    padding: 50px 0 !important;
  }

  .bff-area {
    position: relative;
    width: 100%;
    z-index: 9;
    .bff-tile {
      position: absolute;
      left: calc(50% - 100px);
      top: -23px;
      width: 220px;
      text-align: center;
      line-height: 70px;
      // background: rgba(199, 232, 172, 0.8);
      margin: 0 auto;
      // border: 2px solid #bbbaba;
      height: 70px;
      font-size: 16px;
      border: 4px;
      font-weight: bold;
      box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.14), 0 7px 10px -5px rgba(156, 39, 176, 0.4) !important;
      background-color: #9c27b0 !important;
      color: #fff;
      border-radius: 4px;
    }
    .fe-arrow {
      z-index: 100;
      position: absolute;
    }
  }
  .bff-fe-3 {
