  .backend-area {
    margin-top: 30px;
    min-height: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .dropzone {
    box-sizing: content-box !important;
    background-color: rgba(250, 250, 250, 1);
    // border: 2px dashed #bbbaba;
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14),
      0px 2px 1px -1px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
    padding: 5px;
    text-align: center;
    position: relative;
    h5 {
      font-size: 1rem;
      margin: 5px 0px 15px 5px;
      position: absolute;
      text-align: left;
      color: #232323;
      top: 5px;
      left: 5px;
    }
    &.is-active {
      border: 2px solid #9c27b0 !important;
    }
    &.is-started {
      border: 2px dashed #9c27b0;
    }
  }

  .action-area {
    // margin-top: 60px;
    width: 200px;
    order: 2;
    > button {
      position: relative;
      .middle-icon {
        position: absolute;
        top: 12px;
        left: 90px;
      }
      .move-vert-icon {
        position: absolute;
        left: -6px;
        top: 20px;
        font-size: 30px;
      }
      > span {
        display: block;
        margin-top: 22px;
      }
      color: #fff;
      width: 200px;
      cursor: move;
      margin-left: 30px;
      margin-bottom: 15px;
      font-size: 14px;
      height: 80px;
      font-weight: bold;
      background-color: #fafafa;
      box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14),
        0px 2px 1px -1px rgba(0, 0, 0, 0.12);
      border-radius: 4px;
      // &:nth-child(1) {
      //   margin-left: 20px;
      // }
      // &:nth-child(3) {
      //   width: 21%;
      // }
    }
  }

  .svc-tile {
    // cursor: move;
    width: 200px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    height: 50px;
    margin: 0 0 15px 15px;
    font-size: 14px;
    box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.14), 0 7px 10px -5px rgba(156, 39, 176, 0.4) !important;
